.site {
  max-width: 1136px;
  margin: 0 auto; }

.site-header {
  height: 97px;
  width: 100%;
  background-color: #4d4d4d; }
  .site-header .logo {
    width: 97px;
    background-color: #efefef;
    background-image: url("../images/header/logo-pattern.png");
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center; }
    .site-header .logo .logotype {
      border: 2px dashed #d5d5d5;
      border-radius: 8px; }
  .site-header .header-title {
    font-family: 'Ubuntu';
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    color: #cacaca;
    padding-left: 27px;
    padding-top: 0px;
    height: 100%;
    vertical-align: top; }
    .site-header .header-title .main-title {
      font-weight: 700;
      margin: 0;
      margin-top: -10px;
      font-size: 44px;
      transform: skewX(-10deg); }
      .site-header .header-title .main-title::before {
        content: 'B';
        font-family: 'BTC';
        color: #f3993e; }
    .site-header .header-title .sub-title {
      font-weight: 300;
      font-size: 13px;
      transform: skewX(-12deg);
      margin: 0;
      padding-left: 32px;
      margin-top: -21px; }
  .site-header .header-stats {
    display: inline-block;
    height: 100%;
    vertical-align: top;
    margin-left: 55px;
    text-align: center; }
    .site-header .header-stats .header-navigation {
      margin-top: 17px; }
      .site-header .header-stats .header-navigation ul li {
        display: inline-block;
        margin-right: 12px; }
        .site-header .header-stats .header-navigation ul li a {
          text-decoration: none;
          font-size: 20px;
          color: #e6e6e6; }
    .site-header .header-stats .header-cource {
      font-size: 16px;
      color: #b9b9b9;
      font-weight: 300;
      text-decoration: none;
      display: inline-block; }
      .site-header .header-stats .header-cource .icon {
        padding-right: 2px; }
  .site-header .language-picker {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-left: 48px; }
    .site-header .language-picker .language {
      display: inline-block;
      color: #b9b9b9;
      font-weight: 300;
      font-size: 20px;
      position: relative; }
      .site-header .language-picker .language .globe {
        vertical-align: middle;
        padding-right: 5px; }
      .site-header .language-picker .language .more {
        position: absolute;
        right: -14px;
        top: -1px; }
  .site-header .profile {
    display: inline-flex;
    vertical-align: top;
    height: 100%;
    align-items: center;
    margin-left: 30px; }
    .site-header .profile .profile-avatar {
      align-items: center;
      width: 65px;
      height: 65px;
      border: 2px solid #40a44e;
      border-radius: 36em;
      justify-content: center;
      display: flex;
      padding-top: 16px;
      overflow: hidden; }
    .site-header .profile .profile-stats {
      display: flex;
